@use '../base/variables' as v;
@use '../base/mixins' as m;

.header{
    background-color: v.$verde;
    @include m.tablet{
        &.fijo{
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .3rem .3rem .3rem #333;
        }        
    }
    h1{
        color: v.$blanco;
        text-align: center;
        @include m.tablet{
            margin: 0;
        }
    }
    .contenido-header{
        @include m.tablet{
            padding: 2rem;

            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}
.navegacion-principal{
    @include m.tablet{
        display: flex;
        gap: 2rem;
    }
    a{
        display: block;
        color: v.$blanco;
        font-size: 2.4rem;
        text-align: center;
        margin-bottom: 1rem;
        @include m.tablet{
            margin: 0;
        }
        &:hover{
            color: v.$amarillo;
        }
    }
}
